.wrapper-container {
  width: 100%;
  height: 250px;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  display: flex;
}

.demo-container {
  height: 100%;
  width: 80%;
  max-width: 1200px;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

@media screen and (max-width: 900px) {
  .demo-container {
    width: 100%;
    min-width: 300px;
  }
}

.demo-container > img {
  width: 100%;
  filter: opacity();
  position: absolute;
}

.kenburns-top {
  transform-origin: 50% 100%;
  animation: kenburns-bottom 20s ease-in-out infinite alternate-reverse both;
}

.image-container {
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.imagetext.title {
  background-image: url("text-voxel-demo-animations.9b7ea657.png");
  animation: hue-rotate-animation 8s infinite;
}

.imagetext.author {
  filter: opacity(.6);
  background-image: url("text-by-fredy-sandoval.58a7e3f5.png");
}

.imagetext {
  height: 100%;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: absolute;
}

@keyframes kenburns-bottom {
  0% {
    transform: scale(3);
  }

  100% {
    transform: scale(1);
  }
}

@-webkit-keyframes hue-rotate-animation {
  0% {
    -webkit-filter: hue-rotate() opacity(.7);
  }

  50% {
    -webkit-filter: hue-rotate(360deg) opacity(.7);
  }

  100% {
    -webkit-filter: hue-rotate() opacity(.7);
  }
}

@keyframes hue-rotate-animation {
  0% {
    filter: hue-rotate() opacity(.7);
  }

  50% {
    filter: hue-rotate(360deg) opacity(.7);
  }

  100% {
    filter: hue-rotate() opacity(.7);
  }
}

.line {
  border-top: 1px solid #c4c4c4;
  margin-top: 15px;
}

